body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-family: monospace;
  font-size: 16px;
  color: #4bc3ff;
}

a {
  cursor: pointer;
}

a:hover {
  color: #2aadee;
}

.preview {
  display: none;
  width: 100%;
