/* -------- Hannah --------- */

.hannah {
  width: 150px;
  height: 150px;
  position: fixed;
  border: 15px black solid;
  border-radius: 50%;
  top: 45%;
  left: 43%;
}
.glowy-container {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  -webkit-box-shadow: 0 0 10px #000;
          box-shadow: 0 0 10px #000;
  z-index: 500;
  position: absolute;
  background-color: #fff;
  cursor: pointer;
}
.glowy {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.glowy:hover {
  -webkit-animation: neon 1.3s ease-in-out infinite alternate;
  animation: neon 1.3s ease-in-out infinite alternate;
}
.glow-talk {
  -webkit-animation: neon 1.3s ease-in-out infinite alternate;
  animation: neon 1.3s ease-in-out infinite alternate;
}
@-webkit-keyframes neon {
  from {
    -webkit-box-shadow: 0 0 10px #fbff05, 0 0 20px #fbff05, 0 0 30px #fbff05, 0 0 40px #fbff05, 0 0 70px #36A9F7, 0 0 80px #36A9F7, 0 0 100px #36A9F7, 0 0 150px #36A9F7;
            box-shadow: 0 0 10px #fbff05, 0 0 20px #fbff05, 0 0 30px #fbff05, 0 0 40px #fbff05, 0 0 70px #36A9F7, 0 0 80px #36A9F7, 0 0 100px #36A9F7, 0 0 150px #36A9F7;
    -webkit-filter: brightness(60%) grayscale(100%);
            filter: brightness(60%) grayscale(100%);
  }
  to {
    -webkit-box-shadow: 0 0 5px #fbff05, 0 0 10px #fbff05, 0 0 15px #fbff05, 0 0 20px #fbff05, 0 0 35px #36A9F7, 0 0 40px #36A9F7, 0 0 50px #36A9F7, 0 0 63px #36A9F7;
            box-shadow: 0 0 5px #fbff05, 0 0 10px #fbff05, 0 0 15px #fbff05, 0 0 20px #fbff05, 0 0 35px #36A9F7, 0 0 40px #36A9F7, 0 0 50px #36A9F7, 0 0 63px #36A9F7;
    -webkit-filter: brightness(100%) grayscale(0%);
            filter: brightness(100%) grayscale(0%);
  }
}
@keyframes neon {
  from {
    -webkit-box-shadow: 0 0 10px #fbff05, 0 0 20px #fbff05, 0 0 30px #fbff05, 0 0 40px #fbff05, 0 0 70px #36A9F7, 0 0 80px #36A9F7, 0 0 100px #36A9F7, 0 0 150px #36A9F7;
            box-shadow: 0 0 10px #fbff05, 0 0 20px #fbff05, 0 0 30px #fbff05, 0 0 40px #fbff05, 0 0 70px #36A9F7, 0 0 80px #36A9F7, 0 0 100px #36A9F7, 0 0 150px #36A9F7;
    -webkit-filter: brightness(60%) grayscale(100%);
            filter: brightness(60%) grayscale(100%);
  }
  to {
    -webkit-box-shadow: 0 0 5px #fbff05, 0 0 10px #fbff05, 0 0 15px #fbff05, 0 0 20px #fbff05, 0 0 35px #36A9F7, 0 0 40px #36A9F7, 0 0 50px #36A9F7, 0 0 63px #36A9F7;
            box-shadow: 0 0 5px #fbff05, 0 0 10px #fbff05, 0 0 15px #fbff05, 0 0 20px #fbff05, 0 0 35px #36A9F7, 0 0 40px #36A9F7, 0 0 50px #36A9F7, 0 0 63px #36A9F7;
    -webkit-filter: brightness(100%) grayscale(0%);
            filter: brightness(100%) grayscale(0%);
  }
}

 /* -------- RepeatWords ------ */
.display-box-container {
  min-height: 50px;
  padding: 10px;
  border: 2px gold dashed;
  text-align: center;
  letter-spacing: .6px;
  line-height: 20px;
  font-size: 24px;
}
.spinner {
  margin: 10px auto 0;
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: #4bc3ff;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% { -webkit-transform: scale(0) }
  40% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  } 40% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }
}

.modal-style {
  position: fixed;
  background-color: #fff;
  border-radius: 5px;
  top: 50px;
  left: 10%;
  width: 75%;
  min-height: 200px;
  max-height: 550px;
  padding: 10px 20px;
  z-index: 1000;
  font-size: 20px;
  -webkit-box-shadow: 0 2px 4px 0 #7e7e7e;
          box-shadow: 0 2px 4px 0 #7e7e7e;
  border: solid 1px #c2c5ca;
  overflow: auto;
}

#zone-music {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

@media screen and (max-width: 481px) {
  .modal-style {
    width: inherit;
    left:0
  }
  .hannah {
    height: 100px;
    width: 100px;
    border: 5px black solid;
    left: 36%;
    top: 38%;
  }
}



